import React, {useState} from 'react';
import firebase from "firebase/app";
import Form from "react-bootstrap/Form";
import {Button, Col, Container, Image, Row} from "react-bootstrap";
import {userref, usersSet} from "../../Fire/Firebase";
import {withRouter} from 'react-router-dom'
import ReactImageUploadComponent from 'react-images-upload'
import './user.css'
import ImageUploading from "react-images-uploading";

const UserInfoForm = (props) => {
    const userUid = firebase.auth().currentUser.uid
    const initalVal = {
        name:'',
        lastName: '',
        school:'',
        major: '',
        year: '',
        bio: ''

    }
    const [infoData, setInfoData] = useState(initalVal)
    const [image, setImage] = useState(null)
    console.log(image)

    const bioData = (e) => {
        setInfoData({...infoData , [e.target.name]: e.target.value})
    }
    const picChan = (e) => {
        setImage(e.target.files[0])
    }
    const {name , lastName , school, major, year, bio} = infoData
     const bioSub = (ev) => {
        ev.preventDefault()
        usersSet.doc(userUid).update({
            name: name,
            lastName: lastName,
            school: school,
            major: major,
            year: year, bio: bio
        }).then((res) => {

            userref.child(userUid).put(image).then(() => {

                props.history.push('/user-file')
            })
        })
     }


    return (
        <div className='mt-5'>
            <Container>
                <Row>
                    <Col>
                        <div className='text-center'>
                            <h1 className='mt-3 mb-4'>Personal Information</h1>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col className='col-md-6 offset-md-3'>
                            <Form onSubmit={bioSub}>
                                <Row>
                                    <Form.Group as={Col} controlId="formGridCity">
                                        <Form.Label>Name</Form.Label>
                                        <Form.Control name='name' value={name} onChange={bioData} />
                                    </Form.Group>
                                    <Form.Group as={Col} controlId="formGridZip">
                                        <Form.Label >Last Name</Form.Label>
                                        <Form.Control name='lastName' value={lastName} onChange={bioData} />
                                    </Form.Group>
                                </Row>

                                <Form.Group controlId="formGridAddress2">
                                    <Form.Label>School</Form.Label>
                                    <Form.Control name='school' value={school} onChange={bioData} />
                                </Form.Group>
                                <Row>
                                    <Form.Group as={Col} controlId="formGridCity">
                                        <Form.Label>Major</Form.Label>
                                        <Form.Control name='major'  value={major} onChange={bioData} />
                                    </Form.Group>
                                    <Form.Group as={Col} controlId="formGridZip">
                                            <Form.Label>Year</Form.Label>
                                            <Form.Control as="select" name='year' value={year} onChange={bioData} defaultValue="Choose...">
                                                <option>Choose...</option>
                                                <option>Fisrt</option>
                                                <option>Second</option>
                                                <option>Thrid</option>
                                                <option>Fourth</option>
                                            </Form.Control>
                                        </Form.Group>
                                </Row>
                                <Form.Group controlId="exampleForm.ControlTextarea1">
                                    <Form.Label>Bio Desc</Form.Label>
                                    <Form.Control as="textarea" name='bio' value={bio} onChange={bioData} rows={3} />
                                </Form.Group>
                                {/*<img className='img-up'  src={image}></img>*/}
                                {/*<ImageUploading value={''} onChange={picChan}></ImageUploading>*/}
                                {/*<Form.Group className="position-relative mb-3">*/}
                                {/*    <Form.Label>Upload Resume and Image</Form.Label>*/}
                                {/*    <Form.Control*/}
                                {/*        type="file"*/}
                                {/*        required*/}
                                {/*        name=""*/}
                                {/*        onChange={picChan}*/}
                                {/*        // isInvalid={!!errors.file}*/}
                                {/*    />*/}
                                {/*</Form.Group>*/}
                                <div className="custom-file">
                                    <input type="file"  className="custom-file-input" onChange={picChan}></input>
                                        <label className="custom-file-label" htmlFor="customFile">Choose Image and Resume</label>
                                </div>

                                <div className='text-center'>
                                    <Button variant="primary" type="submit">
                                   Let's go
                                    </Button>
                                </div>
                            </Form>
                    </Col>
                </Row>
            </Container>
        </div>
    );
};

export default withRouter(UserInfoForm);