import React, {useState} from 'react';
import {Container, Form, Button, Col, Row, Alert, CloseButton} from "react-bootstrap";
import firebase, {signInD, usersSet} from "../../Fire/Firebase";
import { withRouter } from 'react-router-dom'
import './login.css'
import {IoLogoGoogle, IoLogoFacebook} from 'react-icons/io'


const Signin = (props) => {
    const [signinData , setSignInData] = useState({email:'', password: ''})
    const [error,  setError] = useState(null)
    const [loading, setLoading] = useState(false)
    const onCha = (e) => {
        setSignInData({...signinData, [e.target.name] : e.target.value})
        console.log(signinData)
    }
    const onSub = (e) => {
        e.preventDefault()
        const {email, password} = signinData

            signInD.signInWithEmailAndPassword(email, password).then( res => {
                console.log(res)

                    props.history.push("/userinfo")

            }).catch((error) => {
                setError(error.message)
            })



    }

    const gok = () => {
        const googlePro = new firebase.auth.GoogleAuthProvider()
        firebase.auth().signInWithPopup(googlePro).then((res) => {
            props.history.push("/userinfo")
        }).catch((err) => {
            setError(err.message)
        })

    }
    const face = () => {
        const faceBok = new firebase.auth.FacebookAuthProvider()
        firebase.auth().signInWithPopup(faceBok).then((res) => {
            props.history.push("/userinfo")
        }).catch((err) => {
            setError(err.message)
        })

    }

    const logout = () => {
        firebase.auth().signOut().then(() => {

        })
    }
    return (
        <div className='top-div'>
            <Container>
                <Row>
                    <Col className='col-md-6 offset-md-3'>
                        {error ? <div><Alert className='text-center' onClose={() => setError(false)} variant='danger' dismissible>

                            {error}</Alert></div>: null}
                    </Col>
                </Row>

                <Row>


                    <Col className='col-md-6 offset-md-3'>
                        <div className='text-center'>
                            <h1 className='h1-top'>Login</h1>
                        </div>
                        <Form onSubmit={onSub}>
                            <Form.Group className='mb-3' controlId="formBasicEmail">

                                <Form.Control type="email" placeholder="Enter email" onChange={onCha} name='email' value={signinData.email} />

                            </Form.Group>

                            <Form.Group controlId="formBasicPassword">

                                <Form.Control type="password" placeholder="Password" onChange={onCha} name='password' value={signinData.password}/>
                            </Form.Group>

                            <div className='text-center'>
                                <Button variant="outline-danger" type="submit">
                                    Submit
                                </Button>
                            </div>


                        </Form>


                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className='text-center'>
                            <p className='mt-3'>Sign in with</p>

                            <IoLogoGoogle className='ioco gog' onClick={gok}></IoLogoGoogle>
                            <IoLogoFacebook className='ioco fac' onClick={face}></IoLogoFacebook>

                            <p className='mt-3'>Dont have an account <a>Sign up</a></p>


                        </div>
                    </Col>
                </Row>

            </Container>


        </div>
    );
};

export default withRouter(Signin);