
import {BrowserRouter , Route} from "react-router-dom";
import Ja from "./components/MainPage";
import Forma from "./components/postform/form";
import Signin from "./components/signin/signin";
import Signup from "./components/signup/signup";
import UserInfo from "./components/userInfo/userInfo";
import firebase from "./Fire/Firebase";
import NavBar from "./components/ui/navbar";
import Userdata from "./components/userInfo/UserInfoForm";
import UserFile from "./components/userInfo/userFile";
import PostInfo from "./components/postsinfo/postInfo";
import Landing from "./components/ui/Landing";
import NotificationsUser from "./components/userInfo/NotificationsUser";
import Userposts from "./components/postsinfo/userposts";
import Message from "./components/message/message";

function App() {

  return (
      <BrowserRouter>

          <NavBar></NavBar>
          <Route path='/landing'>
              <Landing></Landing>
          </Route>
          <Route exact path='/home'>
              <Ja></Ja>
          </Route>
          <Route exact path='/login'>
              <Signin></Signin>
          </Route>
          <Route exact path='/post-info:id'>
              <PostInfo></PostInfo>
          </Route>
          <Route exact path='/signup'>
              <Signup></Signup>
          </Route>
          <Route path='/post'>
              <Forma></Forma>
          </Route>
          <Route path='/notifications:userid'>
              <NotificationsUser></NotificationsUser>
          </Route>
          <Route path='/userinfo:id'>
              <UserInfo></UserInfo>
          </Route>
          <Route path='/register'>
              <Signup></Signup>
          </Route>
          <Route path='/userdata'>
              <Userdata></Userdata>
          </Route>
          <Route path='/user-file'>
              <UserFile></UserFile>
          </Route>
          <Route path='/userposts'>
              <Userposts></Userposts>
          </Route>
          <Route path='/messages'>
              <Message></Message>
          </Route>

      </BrowserRouter>
  );
}
firebase.auth().onAuthStateChanged((user) => {
    if(user){
        const uid = user.uid;
    }
})

export default App;
