import React, {useEffect, useState} from 'react';
import Form from "react-bootstrap/Form";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import {Button, Container} from "react-bootstrap";
import {imagesCom, poster, ref, userref, usersSet} from "../../Fire/Firebase";
import {withRouter} from 'react-router-dom'
import firebase from "firebase/app";
import './form.css'





const Forma = (props) => {

    const [userData, setUserData] = useState(null)
    const userUid =  firebase.auth().currentUser.uid
    useEffect(() => {

        usersSet.doc(userUid).get().then((res)=>{
            setUserData(res.data())
        })
    }, [])
    const initals = {
        title: '',
        school:'',
        type:'',
        looking: '',
        description: '',
        help:''

    }

    const [image, setImage] = useState(null)
    const [formData , setFormData] = useState(initals)

    const onSub = (e) => {
        //set the data from the postform to the database firebase
        e.preventDefault()
        imagesCom.child(image.name).put(image).on('state_changed',
            (ob) => {

            },
            (error) => {

            },
            (done) => {
                imagesCom.child(image.name).getDownloadURL().then((url) => {
                    poster.add({...formData, uid: userUid ,username: userData.name, likes: 0, comments: 0, url: url,  email:userData.email, userLast:userData.lastName}).then(() => {
                        props.history.push('/home')
                    }).catch((err) => {
                        console.log(err)
                    })

                })
            }
        )


    }




    const onCha = (e) => {
        setFormData({...formData, [e.target.name]:e.target.value})


    }
    const imageChange = (e) => {
        setImage( e.target.files[0])

    }


    return (

        <div className='mt-5'>
            <Container>
                <Row>
                    <Col className='col-md-6 offset-md-3'>
                        <h1 className='title-1' >
                            Post Idea
                        </h1>

                    </Col>
                </Row>
                <Row>
                    <Col className='col-md-6 offset-md-3'>




                                <Form onSubmit={onSub}>

                                <Row>
                                    <Form.Group as={Col} controlId="formGridEmail">
                                        <Form.Label className='lab'>App Title</Form.Label>
                                        <Form.Control   name='title' value={formData.title} onChange={onCha}/>

                                    </Form.Group>
                                </Row>
                                    <Row className='mt-3'>
                                        <Form.Group as={Col} controlId="formGridCity">
                                            <Form.Label className='lab'>School</Form.Label>
                                            <Form.Control  value={formData.school} name='school' onChange={onCha}/>
                                        </Form.Group>

                                    </Row>
                                    <Row>
                                        <Form.Group as={Col} controlId="formGridState" className='mt-3'>
                                            <Form.Label className='lab'>Category</Form.Label>
                                            <Form.Control as="select" name='type'  value={formData.type} defaultValue="Choose..." onChange={onCha}>
                                                <option>Choose...</option>
                                                <option>Mobile App</option>
                                                <option>Website</option>
                                                <option>Software</option>
                                                <option>Hardware</option>
                                            </Form.Control>
                                        </Form.Group>
                                        <Form.Group as={Col} className='mt-3'>
                                            <Form.Label className='lab'>Looking For</Form.Label>
                                            <Form.Control as="select" name='Looking for'  value={formData.look} defaultValue="Choose..." onChange={onCha}>
                                                <option>Choose...</option>
                                                <option>Designer</option>
                                                <option>Programmer</option>
                                                <option>Marketing</option>
                                                <option>Music</option>
                                                <option>Video/Photography</option>
                                                <option>Legal</option>
                                            </Form.Control>
                                        </Form.Group>


                                    </Row>
                                    <Form.Group controlId="exampleForm.ControlTextarea1" className='mt-3'>
                                        <Form.Label className='lab' >Describe your idea</Form.Label>
                                        <Form.Control as="textarea" rows={3} name='description' value={formData.description} onChange={onCha} />

                                    </Form.Group> <Form.Group controlId="exampleForm.ControlTextarea1">
                                    <Form.Label className='lab' >What do you need help with</Form.Label>
                                        <Form.Control as="textarea" rows={3} name='help' value={formData.help} onChange={onCha} />

                                     </Form.Group>




                                    <div className='text-center mt-4'>
                                        <Form.Label className='lab' >Post Image</Form.Label>
                                        <Form.Group>

                                        <Form.Control  onChange={imageChange} type='file'></Form.Control>
                                    </Form.Group>
                                    </div>

                                    <div className='text-center mt-3'>
                                        <Button className='color-bu' type='submit'>Submit</Button>
                                    </div>


                                </Form>

                    </Col>
            </Row>
        </Container>

            
        </div>
    );
};

export default withRouter(Forma);