import React, {useState} from 'react';
import firebase, {signInD, usersSet} from "../../Fire/Firebase";
import {Button, Col, Container, Form, Row, Alert} from "react-bootstrap";
import {withRouter} from 'react-router-dom'
import '../signin/login.css'
import {IoLogoFacebook, IoLogoGoogle} from "react-icons/io";

const Signup = (props) => {
    const [signinData , setSignInData] = useState({email:'', password: ''})
    const [erro, setError] = useState(null)
    const [succes, setSucess] = useState(null)

    const onCha = (e) => {
        setSignInData({...signinData, [e.target.name] : e.target.value})
        console.log(signinData)
    }
    const onSub = (e) => {
        e.preventDefault()
        const {email, password} = signinData
        firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION).then(() => {
            signInD.createUserWithEmailAndPassword(email, password).then( res => {
                console.log(res)
                // usersSet.doc(res.user.uid).set({email: res.user.email}).then(() => {
               usersSet.doc(res.user.uid).set({email: email}).then(() =>{
                   props.history.push("/userdata")
               })

                // })


            }).catch((err) => {
                setError(err.message)
            })
        })


    }
    const goo = () => {
        const googlePro = new firebase.auth.GoogleAuthProvider()
        firebase.auth().signInWithPopup(googlePro).then((res) => {
            if(res){
                setSucess('Succes. You are being redirected')
                props.history.push('/userdata')
            }
        }).catch((errr) => {
            setError(errr.message)
        })

    }
    const fcb = () => {
        const faceBok = new firebase.auth.FacebookAuthProvider()
        firebase.auth().signInWithPopup(faceBok).then((res) => {


                props.history.push('/userdata')

        }).catch((errr) => {
            setError(errr.message)
        })

    }

    return (
        <div className='top-div'>
            <Container>
                <Row>
                    <Col>
                        {erro ?  <Alert className='text-center' variant='danger'>{erro}</Alert> : null}

                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className='text-center'>
                            <h1>Register</h1>
                        </div>
                    </Col>
                </Row>
                <Row>


                    <Col className='col-sm-12 col-md-6 offset-md-3'>
                        <Form onSubmit={onSub}>
                            <Form.Group controlId="formBasicEmail">

                                <Form.Control type="email" placeholder="Enter email" onChange={onCha} name='email' value={signinData.email} />
                                <Form.Text className="text-muted">
                                    We'll never share your email with anyone else.
                                </Form.Text>
                            </Form.Group>

                            <Form.Group controlId="formBasicPassword">

                                <Form.Control type="password" placeholder="Password" onChange={onCha} name='password' value={signinData.password}/>
                            </Form.Group>

                            <div className='text-center'>
                                <Button variant="outline-danger" type="submit">
                                    Submit
                                </Button>
                            </div>

                        </Form>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className='text-center'>
                            <p className='mt-3'>Sign up with</p>

                            <IoLogoGoogle className='ioco' onClick={goo}></IoLogoGoogle>
                            <IoLogoFacebook className='ioco' onClick={fcb}></IoLogoFacebook>

                            <p className='mt-3'>Already have an account <a>SignIn</a></p>


                        </div>
                    </Col>
                </Row>

            </Container>


        </div>
    )
};

export default withRouter(Signup);