import React, {useEffect, useState} from 'react';
import firebase from "firebase/app";
import {poster} from "../../Fire/Firebase";
import {postData} from "../../Fire/functions";
import {Alert, Badge, Button, Card, CardGroup, Container, Modal, Row} from "react-bootstrap";
import Col from "react-bootstrap/Col";
import './postinfo.css'

const Userposts = () => {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const userId = firebase.auth().currentUser.uid
    const [allpostUser , setallPostUser] = useState(null)
    useEffect(() => {
        const datares =  poster.where("uid", "==", `${userId}`).onSnapshot((res) => {
            console.log(res)
            const allppstData = postData(res);
            setallPostUser(allppstData)
        })


    },[])
    console.log(allpostUser)

    const deleteCom = (docid) => {
        poster.doc(docid).delete().then(() => {
            setShow(true)
        })

    }


console.log(allpostUser)
    return (
        <Container>
            <Row>
                <Col>
                    <Alert show={show} variant="success">
                        <Alert.Heading>How's it going?!</Alert.Heading>
                        <p>
                            Duis mollis, est non commodo luctus, nisi erat porttitor ligula, eget
                            lacinia odio sem nec elit. Cras mattis consectetur purus sit amet
                            fermentum.
                        </p>
                        <hr />
                        <div className="d-flex justify-content-end">
                            <Button onClick={() => setShow(false)} variant="outline-success">
                                Close me y'all!
                            </Button>
                        </div>
                    </Alert>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Modal show={show} onHide={handleClose}>
                        <Modal.Header closeButton>
                            <Modal.Title>Modal heading</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={handleClose}>
                                Close
                            </Button>
                            <Button variant="primary" onClick={handleClose}>
                                Save Changes
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </Col>
            </Row>
            <Row className='mt-5'>
                <Col className='col-md-6 offset-md-3 col-sm-12'>
                    <h4>My Posts</h4>
                </Col>
            </Row>
           <Row >
               <Col className='col-md-6 offset-md-3'>






                   { allpostUser ? allpostUser.map((pos) => (
                       <Card className='card-sha mb-4' border='light'>
                           <Card.Img  className='card-pho' src={pos.url}/>

                           <Card.Body>


                           <p>{pos.description}</p>
                           <p>{pos.type}</p>
                           <p>{pos.description}</p>
                           <p>{pos.description}</p>
                           </Card.Body>
                           <div className='text-center mb-3'>
                               <Button onClick={handleShow} >Edit</Button>
                               <Button onClick={() => {
                                   deleteCom(pos.id)
                               }} >Delete</Button>
                           </div>
                       </Card>

                   )) : <h1>No res</h1>}
               </Col>
           </Row>
        </Container>
    );
};

export default Userposts;