import React, {useEffect, useState} from 'react';
import { useLocation } from 'react-router-dom'
import {poster} from "../../Fire/Firebase";
import {Container, Row, Col ,Card, Badge, Modal, Button} from "react-bootstrap";
import Form from "react-bootstrap/Form";
import {fetchDat} from "../../Fire/functions";
import './postinfo.css'
import firebase from "firebase/app";
const PostInfo = () => {

    const urlid = useLocation().pathname.toString().split(':').pop()
    const [postinfo, setPostInfo] = useState(null)
    const [show, setShow] = useState(false)
    const [messageValue, setMessaheValue] = useState('')
    const [allposts , setAllPost ] = useState(null)

    const [comments, setCommets] = useState(null)
    const [auth, setAuth] = useState(null)




    useEffect(() => {


        if(firebase.auth().currentUser === null){
            setAuth(true)
        }else{
            poster.doc(urlid).get().then((eres) =>{
                setPostInfo(eres.data())
                setCommets(eres.data().comments)
            })
            poster.doc(urlid).collection('allcomments').onSnapshot((doc) => {
                const FetDataBack = fetchDat(doc)
                setAllPost(FetDataBack)
            })
        }

    }, [])

    const mesgeCahnge = (e) => {
        setMessaheValue(e.target.value)
    }
    const commentSub = (e) => {
        e.preventDefault()
      console.log(messageValue)
        poster.doc(urlid).collection('allcomments').add( {message: messageValue}).then((res) => {
            console.log(res)
            setShow(false)
            setMessaheValue('')
        }).then(() => {
            poster.doc(urlid).update({comments:comments + 1 }).then((res) => {
                console.log(res)
            })
        })
    }
    return (


        <div className='mt-5'>
            <Container>
                <Row>
                    {auth ? <h1>Plese Sign in to comment and like</h1>: null}
                </Row>
                <Row>
                    <Col className='col-md-6 offset-md-3 col-sm-10 offset-sm-1'>
                        {
                            postinfo ?
                                <Card className='card-b' >
                                    <Card.Img className='image-pro'  src={postinfo.url} />
                                    <Card.Body>
                                        <Card.Title>{postinfo.title}</Card.Title>
                                        <p><span>{postinfo.username}</span><span>{postinfo.lastName}</span></p>
                                        <Badge className='mr-auto' variant='primary'>{postinfo.email}</Badge>
                                        <Badge variant='secondary'>{postinfo.school}</Badge>
                                        <Badge variant='danger'>{postinfo.type}</Badge>

                                        <p>{postinfo.languages}</p>
                                        <p>{postinfo.description}</p>


                                    </Card.Body>
                                </Card> : null
                        }

                        {
                            auth ? null:  <Card>
                                <Card.Header>
                                    <Card.Title>Comments</Card.Title>
                                    <Button onClick={() => {setShow(true)}}> Submit commet</Button>
                                </Card.Header>

                                <Card.Body>
                                    {
                                        allposts ? allposts.map((post) => (
                                            <p><Badge variant='success'>{post.message}</Badge></p>
                                        )) : <h1>Loading...</h1>
                                    }

                                </Card.Body>
                            </Card>
                        }


                    </Col>


                </Row>
                <Row>
                    <Col>
                        <Modal show={show} onHide={()=>{setShow(false)}} animation={false}>
                            <Modal.Header closeButton>
                                <Modal.Title>Post Comment</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <Form onSubmit={commentSub}>
                                    <Form.Group controlId="exampleForm.ControlTextarea1">

                                        <Form.Control onChange={mesgeCahnge} value={messageValue} as="textarea" rows={3} />
                                    </Form.Group>
                                    <Button type='submit' variant="primary" >
                                        Submit
                                    </Button>
                                </Form>
                            </Modal.Body>

                        </Modal>
                    </Col>
                </Row>
            </Container>
        </div>
    );
};

export default PostInfo;