import React, {Component, useState} from 'react';
import {firestore, poster} from "../Fire/Firebase";
import {fetchDat} from "../Fire/functions";
import Card from "react-bootstrap/Card";
import {withRouter} from 'react-router-dom'

import './MainPage.css'
import {FaCommentAlt, FaDatabase, FaDesktop, FaHome, FaMobile, FaMobileAlt, FaThumbsUp} from 'react-icons/fa'
import {Container, Col, Row} from "react-bootstrap";
import {Facebook} from "react-content-loader";
import firebase from "firebase/app";
import Loader from "./ui/loader";

const MainPage = (props) => {

    const [getPost, setPostData] = useState(null)
    const [getError, setGetError] = useState(null)

    //check for user id



    /// rendering all the post from the database
    useState( () => {

        firestore.collection('comments').onSnapshot((data) => {
            const returndata = fetchDat(data)
            setPostData(returndata)
        })
    })


    const likeSub = (ref, likes, id) => {
       poster.doc(ref).update({likes: likes + 1}).then((res) => {
           // in here we are going to send the notification to the user
           poster.doc(id).collection('notifications').add({

               id: id
           })
       })
    }

    return (
        <div>

            <Container>
                <Row>
                    <Col>
                        <div >
                            <p className=' mt-5 mb-3 h1-up-head' >Current Posts</p>
                        </div>
                    </Col>
                </Row>
                <Row >



                        {
                            getPost ? getPost.map((da) => (
                                <Col xs={12} className='col-sm-12  col-md-6 col  col-lg-4 '>


                                <Card   className='card-sha mb-3 '  id={da.id} border="light" >

                                    <Card.Img  onClick={() => {props.history.push(`/post-info:${da.id}`)}}  className='image-card' src={da.url}></Card.Img>
                                    <Card.Body  >

                                        <Card.Title className='h1-up mb-2'>{da.title}</Card.Title>
                                        {da.type == 'Mobile App' ?  <div className='mb-1'><FaMobile className='fa-icons '/> {da.type}</div>: null}
                                        {da.type == 'Website' ?  <div className='mb-1'><FaDesktop className='fa-icons '/> {da.type}</div>: null}
                                        {da.type == 'Software' ?  <div className='mb-1'><FaDatabase className='fa-icons'/> {da.type}</div>: null}

                                        <div>
                                            <FaHome className='fa-icons'/>{da.school }
                                        </div>

                                        <div className='mt-3'>
                                            <p>{da.description }gsdgsd sg setwet etwet wet wet wetwet wtq ertqwtqwtqwt qwtqw t</p>
                                        </div>
                                        <div  className='com'>
                                                <div>
                                                    <FaThumbsUp onClick={() => {
                                                        likeSub(da.id, da.likes, da.id)
                                                    }} ></FaThumbsUp> {da.likes} Likes
                                                </div>

                                                <div>
                                                    <FaCommentAlt onClick={ () => {props.history.push(`/post-info:${da.id}`)}} ></FaCommentAlt> {da.comments} Comments

                                                </div>


                                        </div>
                                    </Card.Body>
                                </Card>
                                </Col>

                            )) : <Loader/>
                        }


                </Row>


            </Container>


        </div>
    );
};


export default withRouter(MainPage) ;